body {
  display: flex;
  justify-content: center;
  text-align: center;
  width: 100vw;
  min-height: 100vh;
  margin: auto;
  font-size: 1.2em;
  background-color: #0d2633e1;
  color: white;
  font-family:
    "Comic Sans MS",
    sans-serif;
}

.d-none {
  display: none;
}

.calculator-container {
  width: 30vw;
  display: flex;
  flex-direction: column;
  padding-top: 3rem;
}

.answer {
  width: 29.8vw;
  display: flex;
  align-items: center;
  justify-content: right;
  background-color: #0d263300;
  color: white;
  margin: 0.2rem auto;
  height: 3rem;
  position: absolute;
  border: 1px solid white;
}

button {
  height: 3rem;
  border: 1px solid rgba(128, 128, 128, 0.712);
  border-bottom: none;
  border-right: none;
  cursor: pointer;
  z-index: 999;
}

.calc-row-one p {
  padding-right: 0.5rem;
}

.calc-row-two button,
.calc-row-three button,
.calc-row-four button,
.calc-row-five button,
.calc-row-six button {
  width: 33%;
  cursor: pointer;
}

.calc-row-two {
  align-items: flex-start;
  text-align: left;
  padding-bottom: 0%;
  cursor: pointer;
  z-index: 999;
  width: 100%;
}

.calc-row-two button {
  width: 25%;
}

.calc-row-two .divide {
  width: 7.5%;
  margin-bottom: 0%;
  padding: 0%;
  margin-top: 0%;
  position: absolute;
  background-color: orange;
}

.calc-row-six {
  display: flex;
  width: 30vw;
  border-bottom: 1px solid rgba(128, 128, 128, 0.712);
}

.equal-container {
  width: 30vw;
  display: flex;
  justify-content: right;
  margin: auto;
  height: 3rem;
  align-items: flex-end;
  border-bottom: none;
}

.calc-row-six .zero {
  width: 50%;
}

.calc-row-six .dot,
.calc-row-six .equal {
  width: 25.1%;
}

.orange {
  background-color: orange;
  cursor: pointer;
}

.orange-right {
  align-self: flex-end;
  text-align: right;
  margin: 0%;
  width: 25.5%;
  padding: 0%;
  margin-left: 0%;
  cursor: pointer;
  z-index: 999;
}

.orange-right button {
  width: 100%;
}

.numbers-container {
  position: absolute;
  margin: 0%;
  margin-top: 96px;
  padding: 0%;
  display: flex;
  align-items: flex-start;
  text-align: left;
  width: 22.7%;
  z-index: 999;
}

.numbers-six,
.numbers-five,
.numbers-four,
.numbers-three {
  margin: 0%;
  width: 100%;
}

.numbers-six {
  display: flex;
}

.numbers-six .zero {
  width: 66%;
}

.numbers-six .dot {
  width: 34%;
}

ul {
  list-style: none;
}

.navbar {
  display: flex;
  position: absolute;
  top: 4rem;
  right: 3rem;
}

.navbar li {
  margin: 0% 1rem;
}

.math-magicians-header {
  position: absolute;
  top: 2rem;
  left: 3rem;
  font-size: 3rem;
  color: orange;
}

.home-content {
  text-align: left;
  margin: 0% 3rem;
}

.home-h2 {
  margin-top: 10rem;
  color: orange;
}

.calc-contain {
  display: flex;
  margin-top: 10rem;
  margin-left: 2rem;
}

.calc-header {
  margin: 0% 3rem;
  position: absolute;
  left: 0%;
  color: orange;
}

.quote-content {
  justify-content: center;
  width: 80%;
  position: relative;
  margin: auto;
  top: 50%;
}

a {
  text-decoration: none;
  color: orange;
}
